
















import { Component, Prop, Vue } from 'vue-property-decorator';
import SignupForm from "@/components/EmailForm.vue";

@Component({
  components: {
    SignupForm,
  },
})
export default class Email extends Vue {
  @Prop() private isModal!: boolean;
  @Prop() private isOpen!: boolean;
  get shouldShow (): boolean {
      return this.isModal && this.isOpen 
  }

  emitClose() {
      this.$emit("shouldClose");
  }
}
