import Vue from 'vue';
import Vuex from 'vuex';
import { Document, QueryPrismic, GetPostWithUID } from '@/prismic/api';

Vue.use(Vuex);

class State {
    Posts: Array<Document> = new Array<Document>();
    ArePostsLoading:Boolean = false;
    PostLoadedWithError: any = null;
    CurrentPost: Document | null =  null;
}

export default new Vuex.Store({
  state: new State,
  mutations: {
    SetViewablePosts(state, posts: Document[]) {
      state.Posts = posts;
    },
    LoadingPosts(state, isLoading: boolean) {
      state.ArePostsLoading = isLoading;
    },
    SetPostLoadingError(state, error) {
      state.PostLoadedWithError = error;
    },
    SetCurrentPost(state, post: Document | null) {
      state.CurrentPost = post;
    }
  },
  actions: {
    async GetPosts(context) {
        const shouldLoad = context.state.Posts.length <= 0;
        context.commit("LoadingPosts", shouldLoad); //context.state.Posts.length <== 0);
      try {
        let posts = await QueryPrismic()
        context.commit('SetViewablePosts', posts);
      } catch(e) {
        context.commit("SetPostLoadingError", e);
      } finally {
        context.commit("LoadingPosts", false);
      }
    },
    async GetAndSetPost(context, uid) {
      context.commit("LoadingPosts", true);
      let post = context.state.Posts.find(post => post.uid === uid);
      if (post === undefined) {
        post = await GetPostWithUID(uid);
      }
      context.commit("SetCurrentPost", post);
    }
  },
});
