import Prismic from "prismic-javascript";
import Post from "./Post";

export interface Document {
    id: string;
    uid?: string;
    type: string;
    href: string;
    tags: string[];
    slugs: string[];
    lang?: string;
    alternate_languages: string[];
    first_publication_date: string | null;
    last_publication_date: string | null;
    data: any;
}
let apiEndpoint = "https://shuttl.prismic.io/api/v2";

interface PrismicResponse {
    page: number;
    results_per_page: number;
    results_size: number;
    total_results_size: number;
    total_pages: number;
    next_page: string;
    prev_page: string;
    results: Document[];
}

export const QueryPrismic = async (query: string = "", opts = {}): Promise<Document[]> => {
    const api = await Prismic.getApi(apiEndpoint, {});
    const resp: PrismicResponse = await api.query(query, opts);
    return resp.results;
}

export const GetPostWithUID = async (uid: string): Promise<Document> => {
    const api = await Prismic.getApi(apiEndpoint, {});
    return await api.getByUID('blog_post', uid);
}